'use client';

import React, { useState, ChangeEvent, FormEvent } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { Anime, Category } from '@/types/anime';
import { useAnime } from '@/contexts/AnimeContext';

interface AnimeFormProps {
  onClose: () => void;
  editingAnime?: Anime;
}

const AnimeForm: React.FC<AnimeFormProps> = ({ onClose, editingAnime }) => {
  const { addAnime, updateAnime, categories } = useAnime();
  
  const [title, setTitle] = useState(editingAnime?.title || '');
  const [status, setStatus] = useState<'watched' | 'to-watch'>(editingAnime?.status || 'to-watch');
  const [rating, setRating] = useState(editingAnime?.rating || 0);
  const [coverImage, setCoverImage] = useState<string | undefined>(editingAnime?.coverImage);
  const [selectedCategoryIds, setSelectedCategoryIds] = useState<string[]>(editingAnime?.categoryIds || []);
  const [prequels, setPrequels] = useState(editingAnime?.relationships.prequels.join(', ') || '');
  const [sequels, setSequels] = useState(editingAnime?.relationships.sequels.join(', ') || '');
  const [movies, setMovies] = useState(editingAnime?.relationships.movies.join(', ') || '');
  const [notes, setNotes] = useState(editingAnime?.notes || '');
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(false);

  // Manejar cambio en la imagen
  const handleImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (!file.type.startsWith('image/')) {
        setError('El archivo debe ser una imagen.');
        return;
      }
      
      if (file.size > 5 * 1024 * 1024) { // 5MB limit
        setError('La imagen debe ser menor a 5MB.');
        return;
      }

      setIsLoading(true);
      const reader = new FileReader();
      reader.onloadend = () => {
        setCoverImage(reader.result as string);
        setIsLoading(false);
      };
      reader.readAsDataURL(file);
    }
  };

  // Manejar clic en estrellas
  const handleStarClick = (star: number) => {
    setRating(star);
  };

  // Manejar selecci√≥n de categor√≠as
  const handleCategoryToggle = (categoryId: string) => {
    setSelectedCategoryIds(prev => 
      prev.includes(categoryId)
        ? prev.filter(id => id !== categoryId)
        : [...prev, categoryId]
    );
  };

  // Manejar env√≠o del formulario
  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setError('');

    if (!title.trim()) {
      setError('El t√≠tulo es obligatorio.');
      return;
    }

    const animeData: Omit<Anime, 'id' | 'dateAdded'> = {
      title: title.trim(),
      status,
      rating,
      coverImage,
      categoryIds: selectedCategoryIds,
      relationships: {
        prequels: prequels.split(',').map(s => s.trim()).filter(Boolean),
        sequels: sequels.split(',').map(s => s.trim()).filter(Boolean),
        movies: movies.split(',').map(s => s.trim()).filter(Boolean),
      },
      notes: notes.trim() || undefined,
      dateWatched: status === 'watched' ? new Date().toISOString() : editingAnime?.dateWatched,
    };

    if (editingAnime) {
      updateAnime(editingAnime.id, animeData);
    } else {
      const newAnime: Anime = {
        ...animeData,
        id: uuidv4(),
        dateAdded: new Date().toISOString(),
      };
      addAnime(newAnime);
    }

    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-bold text-gray-900">
              {editingAnime ? 'Editar Anime' : 'A√±adir Nuevo Anime'}
            </h2>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 text-2xl"
            >
              √ó
            </button>
          </div>

          {error && (
            <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded mb-4">
              {error}
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-6">
            {/* T√≠tulo */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                T√≠tulo *
              </label>
              <input
                type="text"
                value={title}
                onChange={e => setTitle(e.target.value)}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Nombre del anime"
                required
              />
            </div>

            {/* Estado y Rating */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Estado
                </label>
                <select
                  value={status}
                  onChange={e => setStatus(e.target.value as 'watched' | 'to-watch')}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                >
                  <option value="to-watch">Por ver</option>
                  <option value="watched">Visto</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Calificaci√≥n
                </label>
                <div className="flex space-x-1">
                  {[1, 2, 3, 4, 5].map(star => (
                    <button
                      key={star}
                      type="button"
                      onClick={() => handleStarClick(star)}
                      className={`text-2xl transition-colors ${
                        star <= rating ? 'text-yellow-500' : 'text-gray-300'
                      } hover:text-yellow-400`}
                    >
                      ‚òÖ
                    </button>
                  ))}
                  <span className="ml-2 text-sm text-gray-600 self-center">
                    ({rating}/5)
                  </span>
                </div>
              </div>
            </div>

            {/* Imagen de portada */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Imagen de portada
              </label>
              <input
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                disabled={isLoading}
              />
              {isLoading && (
                <p className="text-sm text-gray-500 mt-1">Cargando imagen...</p>
              )}
              {coverImage && (
                <div className="mt-2">
                  <img
                    src={coverImage}
                    alt="Preview"
                    className="w-32 h-48 object-cover rounded-md border"
                  />
                </div>
              )}
            </div>

            {/* Categor√≠as */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Categor√≠as
              </label>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-2 max-h-40 overflow-y-auto border border-gray-200 rounded-md p-3">
                {categories.map(category => (
                  <label
                    key={category.id}
                    className="flex items-center space-x-2 cursor-pointer"
                  >
                    <input
                      type="checkbox"
                      checked={selectedCategoryIds.includes(category.id)}
                      onChange={() => handleCategoryToggle(category.id)}
                      className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                    />
                    <span
                      className="w-3 h-3 rounded-full"
                      style={{ backgroundColor: category.color }}
                    ></span>
                    <span className="text-sm text-gray-700">{category.name}</span>
                  </label>
                ))}
              </div>
            </div>

            {/* Relaciones */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900">Relaciones</h3>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Precuelas (separadas por coma)
                </label>
                <input
                  type="text"
                  value={prequels}
                  onChange={e => setPrequels(e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Ej: Temporada 1, OVA especial"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Secuelas (separadas por coma)
                </label>
                <input
                  type="text"
                  value={sequels}
                  onChange={e => setSequels(e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Ej: Temporada 3, Final season"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Pel√≠culas relacionadas (separadas por coma)
                </label>
                <input
                  type="text"
                  value={movies}
                  onChange={e => setMovies(e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Ej: Pel√≠cula final, OVA"
                />
              </div>
            </div>

            {/* Notas */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Notas personales
              </label>
              <textarea
                value={notes}
                onChange={e => setNotes(e.target.value)}
                rows={3}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Comentarios, opiniones, recordatorios..."
              />
            </div>

            {/* Botones */}
            <div className="flex justify-end space-x-3 pt-4">
              <button
                type="button"
                onClick={onClose}
                className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 transition-colors"
              >
                Cancelar
              </button>
              <button
                type="submit"
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
              >
                {editingAnime ? 'Actualizar' : 'Guardar'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AnimeForm;
