'use client';

import React from 'react';
import Link from 'next/link';
import { Anime } from '@/types/anime';
import { useAnime } from '@/contexts/AnimeContext';

interface AnimeCardProps {
  anime: Anime;
}

const AnimeCard: React.FC<AnimeCardProps> = ({ anime }) => {
  const { getCategoriesByIds } = useAnime();
  const categories = getCategoriesByIds(anime.categoryIds);

  // Helper para renderizar estrellas
  const renderStars = (rating: number) => {
    const stars = [];
    for (let i = 1; i <= 5; i++) {
      stars.push(
        <span
          key={i}
          className={`text-lg ${i <= rating ? 'text-yellow-500' : 'text-gray-300'}`}
        >
          ‚òÖ
        </span>
      );
    }
    return stars;
  };

  return (
    <Link href={`/animes/${anime.id}`}>
      <div className="bg-white border border-gray-200 rounded-lg p-4 hover:shadow-lg transition-all duration-200 hover:scale-105 cursor-pointer">
        {/* Cover Image */}
        <div className="mb-3">
          {anime.coverImage ? (
            <img
              src={anime.coverImage}
              alt={`Portada de ${anime.title}`}
              className="w-full h-48 object-cover rounded-md"
            />
          ) : (
            <div className="w-full h-48 bg-gray-100 flex items-center justify-center rounded-md border-2 border-dashed border-gray-300">
              <div className="text-center">
                <div className="text-4xl text-gray-400 mb-2">üì∫</div>
                <span className="text-sm text-gray-500">Sin imagen</span>
              </div>
            </div>
          )}
        </div>

        {/* Anime Info */}
        <div className="space-y-2">
          <h3 className="text-lg font-bold text-gray-900 line-clamp-2">
            {anime.title}
          </h3>
          
          {/* Rating */}
          <div className="flex items-center space-x-1">
            {renderStars(anime.rating)}
            <span className="text-sm text-gray-600 ml-2">
              ({anime.rating}/5)
            </span>
          </div>

          {/* Status */}
          <div className="flex items-center">
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
              anime.status === 'watched' 
                ? 'bg-green-100 text-green-800' 
                : 'bg-blue-100 text-blue-800'
            }`}>
              {anime.status === 'watched' ? '‚úì Visto' : '‚è≥ Por ver'}
            </span>
          </div>

          {/* Categories */}
          {categories.length > 0 && (
            <div className="flex flex-wrap gap-1">
              {categories.slice(0, 3).map(category => (
                <span
                  key={category.id}
                  className="inline-flex items-center px-2 py-1 rounded-md text-xs font-medium text-white"
                  style={{ backgroundColor: category.color }}
                >
                  {category.name}
                </span>
              ))}
              {categories.length > 3 && (
                <span className="inline-flex items-center px-2 py-1 rounded-md text-xs font-medium bg-gray-100 text-gray-600">
                  +{categories.length - 3}
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </Link>
  );
};

export default AnimeCard;
