'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import AnimeCard from '@/components/anime/AnimeCard';
import AnimeForm from '@/components/anime/AnimeForm';
import { useAnime } from '@/contexts/AnimeContext';
import { FilterType } from '@/types/anime';

const AnimeListPage: React.FC = () => {
  const { animes, categories } = useAnime();
  const [showForm, setShowForm] = useState(false);
  const [filter, setFilter] = useState<FilterType>('all');
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [searchTerm, setSearchTerm] = useState('');

  // Filtrar animes
  const filteredAnimes = animes.filter(anime => {
    // Filtro por estado
    if (filter !== 'all' && anime.status !== filter) return false;
    
    // Filtro por categor√≠a
    if (selectedCategory !== 'all' && !anime.categoryIds.includes(selectedCategory)) return false;
    
    // Filtro por b√∫squeda
    if (searchTerm && !anime.title.toLowerCase().includes(searchTerm.toLowerCase())) return false;
    
    return true;
  });

  // Estad√≠sticas
  const stats = {
    total: animes.length,
    watched: animes.filter(a => a.status === 'watched').length,
    toWatch: animes.filter(a => a.status === 'to-watch').length,
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-6">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Mis Animes</h1>
              <p className="text-gray-600 mt-1">
                {stats.total} animes ‚Ä¢ {stats.watched} vistos ‚Ä¢ {stats.toWatch} por ver
              </p>
            </div>
            <div className="flex space-x-3 mt-4 md:mt-0">
              <Link
                href="/categorias"
                className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 transition-colors"
              >
                Gestionar Categor√≠as
              </Link>
              <button
                onClick={() => setShowForm(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
              >
                + A√±adir Anime
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 py-6">
        {/* Filtros y b√∫squeda */}
        <div className="bg-white rounded-lg shadow-sm border p-4 mb-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {/* B√∫squeda */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Buscar
              </label>
              <input
                type="text"
                value={searchTerm}
                onChange={e => setSearchTerm(e.target.value)}
                placeholder="Buscar por t√≠tulo..."
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            {/* Filtro por estado */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Estado
              </label>
              <select
                value={filter}
                onChange={e => setFilter(e.target.value as FilterType)}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="all">Todos</option>
                <option value="watched">Vistos</option>
                <option value="to-watch">Por ver</option>
              </select>
            </div>

            {/* Filtro por categor√≠a */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Categor√≠a
              </label>
              <select
                value={selectedCategory}
                onChange={e => setSelectedCategory(e.target.value)}
                className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="all">Todas las categor√≠as</option>
                {categories.map(category => (
                  <option key={category.id} value={category.id}>
                    {category.name}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        {/* Lista de animes */}
        {filteredAnimes.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-6xl text-gray-300 mb-4">üì∫</div>
            <h3 className="text-xl font-medium text-gray-900 mb-2">
              {animes.length === 0 ? 'No hay animes registrados' : 'No se encontraron animes'}
            </h3>
            <p className="text-gray-600 mb-6">
              {animes.length === 0 
                ? 'Comienza a√±adiendo tu primer anime a la colecci√≥n'
                : 'Intenta ajustar los filtros de b√∫squeda'
              }
            </p>
            {animes.length === 0 && (
              <button
                onClick={() => setShowForm(true)}
                className="px-6 py-3 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
              >
                A√±adir mi primer anime
              </button>
            )}
          </div>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {filteredAnimes.map(anime => (
              <AnimeCard key={anime.id} anime={anime} />
            ))}
          </div>
        )}
      </div>

      {/* Formulario modal */}
      {showForm && (
        <AnimeForm onClose={() => setShowForm(false)} />
      )}
    </div>
  );
};

export default AnimeListPage;
